import React, { useState } from 'react'

import LotteryBullet from './bullet';
import './style.css';


const LotteryBoard = (props) => {

    const {onClick} = props;

    const generateNumbers = (size) => {
        return [...Array(size).keys()].map(item => ({id: item+1, selected: false}))
    }

    const [numbers, ] = useState(generateNumbers(60))

    const handleToggle = (number) => {

        const numbersSelected = numbers.filter(number => number.selected)
        if ((numbersSelected.length === 15) && (!number.selected)) {
            return
        }
        number.selected = !number.selected        

        onClick(numbers.filter(number => number.selected));
    }
       
    return (
        <div className="coluna-aposte m-5">
            <ul className="escolhe-numero">
                {numbers.map((item) => <LotteryBullet key={item.id} number={item} onClick={handleToggle}/>)}
            </ul>           
            <div className="container-numeros-selecionados">
                <ul className="numeros-selecionados">
                    {  numbers.filter(number => number.selected).map(number => <li key={number.id}>{number.id}</li>)    }     
                </ul>                
            </div>            
        </div>
    )
}

export default LotteryBoard