import {useState} from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import { useAuth } from '../../blockchain/auth'
import TicketCard from "../TicketCard"
import PigBlack from "../../assets/images/pigblack.svg"
import LotteryBoard from "../LotteryBoard"
import LotteryPay from "../LotteryPay"
import PlayersBoard from "../PlayersBoard"

function PlayGame() {
  const { 
      isSigned,
      account,
      balance,
      myPlayers,
      GetLuck,
       } = useAuth();

  const [selectedNumbers, setSelectedNumbers] = useState([])

  const handleLotteryNumberClick = (numbers) => {
    setSelectedNumbers(numbers);
  }

  const handlePay = async (selectedNumbers, amount) => {
    
    await GetLuck(selectedNumbers, amount)
  }

  return (
      <Container fluid>
        <Row  className='mt-3'>
          <img src={PigBlack} width="400" height="200" className="d-inline-block align-top" alt="" />
          <h2 className="text-center">$ {balance.substring(0, 7)} BNB until now</h2>
        </Row>
        
        <Row>
          <Col lg="9"> 
            <LotteryBoard onClick={handleLotteryNumberClick}></LotteryBoard>    
          </Col>
          <Col lg="3">
            <LotteryPay selectedNumbers={selectedNumbers} action={handlePay}></LotteryPay>    
          </Col>          
        </Row>

        <Row >
          <Col className="mt-3 rarity d-flex align-items-center justify-content-center" style={{textTransform: "uppercase"}}>
            <div className="text-center">
              <h2>My Players</h2>
              <PlayersBoard players={myPlayers} />
            </div>            
          </Col>
        </Row>

        <Row className='mt-3'>
          <Col className="rarity" style={{textTransform: "uppercase"}}> 
            <div className="text-center col-lg-12 col-md-8 col-sm-12">
              <h2>Rarity</h2>
              <p className="normal-text">Each PacCat is unique and more numbers you choose, the rarer your avatar will be.</p>              
              <p className="normal-text">Once PacCat has been minted, it'll possible to see it on OpenSea as well.</p>              
            </div>
          </Col>
        </Row>  

        <Row className='mt-3'> 
          <Col className="d-flex align-items-center justify-content-center" style={{textAlign: "center"}} >
            <div className="m-2"><TicketCard playerNumbers={[...Array(6)]}></TicketCard></div>
            <div className="m-2"><TicketCard playerNumbers={[...Array(7)]}></TicketCard></div>
            <div className="m-2"><TicketCard playerNumbers={[...Array(9)]}></TicketCard></div>
            <div className="m-2"><TicketCard playerNumbers={[...Array(11)]}></TicketCard></div>
          </Col>
        </Row>          

        <Row className='mt-3'> 
          <Col className="d-flex align-items-center justify-content-center" style={{textAlign: "center"}} >
            <div className="m-2"><TicketCard playerNumbers={[...Array(13)]}></TicketCard></div>
            <div className="m-2"><TicketCard playerNumbers={[...Array(14)]}></TicketCard></div>
            <div className="m-2"><TicketCard playerNumbers={[...Array(15)]}></TicketCard></div>
            </Col>
        </Row>         

        <Row  className='mt-3'>
          <Col md="5">
            <div className="normal-text">
              <h2  className="text-center" style={{fontFamily: "ThemeFont"}}>General Rules:</h2>
              <br/>
              
                <p>1 - At the end of the game, 90% of the amount collected on the PACCAT contract will be distributed to the winner</p>
                <p>2 - In case two o more person win the game, the amount collected will be distributed equally among the winners</p>
                <p>3 - The game finishes on 2022/Jun/01</p>
                <p>4 - The winner will be the one who owned at least six numbers announced at the end of the game</p>
              </div>
          </Col>
          <Col md="5">
            <div className="normal-text">
              <h2 className="text-center" style={{fontFamily: "ThemeFont"}}>Play Rules:</h2>
              <br/>            
                <p>5 - You should select from 6 to 15 numbers out of a total of 60</p>
                <p>6 - The cost per number increases ( min 0.005 eth ) with the amount of numbers selected</p>
                <p>7 - You can play as many times as you like</p>
                <p>8 - Each time you play, one token will be minted to your account, with the numbers that you've selected</p>
            </div>
          </Col>       
          <Col md="2">
            <div className="card-back">
              <p className="card-back-text">{account}</p>
            </div>          
          </Col>             
        </Row>      
      </Container>          
  );
}

export default PlayGame;
